<template>
    <div class="my-popups">
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">
                            {{ TabName }} : {{ HistoryItems.UniqueName }}
                        </h5>
                        <button v-on:click="ClosePopup()" type="button" class="close" data-original-title="Close"><em
                                class="fa fa-times"></em></button>

                    </div>
                    <div class="border p-3">
                        <dynamic-form ref="Audithistory" lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                        <tg-list :showCheckBox="false" :IsShowAction="false" :listType="['List']" ModuleName="Asset"
                            SubModuleCode="Asset" IdentityColumn="RelatedinfoID" :HeaderText="$t('AssetManagement')"
                            :widgets="widgets" :ListData="viewHistroyData" :HeaderData="Header"
                            :callbackfunction="FetchData" :LegendArray="LegendArray" :SearchFields="leftSearchFields"
                            :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NorecordfoundText')"
                            :TotalRecords="TotalRecord" :RenderRowActionMethod="renderActions"
                            :ListDataCallBackFunction="FetchData" v-on:PagerButtonClick="pagerMethod"
                            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
                            <template v-slot:slotdata="{ data }">

                                <template v-if="data.column.COLUMN_NAME == 'audit_date'">
                                    <span class="text-dark text-center" :title="data.column.COLUMN_NAME">
                                        {{ $options.filters.formatDate(data.row.audit_date) }}
                                    </span>
                                </template>
                                <template v-if="data.column.COLUMN_NAME == 'StatusId'">
                                    <em class="active-disabled" v-if="data.row.StatusId == 1">
                                        <span class="btn d-inline-block w-80 text-center text-truncate">{{ $t('Pass')
                                        }}</span>
                                    </em>
                                    <em class="active-disabled" v-if="data.row.StatusId == 0">
                                        <span class="btn d-inline-block w-80 text-center text-truncate">{{ $t('Fail')
                                        }}</span>
                                    </em>
                                </template>
                            </template>
                        </tg-list>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import DataService from '../../service/DataService'
export default {
    props: {
        HistoryItems: {
            type: Array,
            required: true
        },
        TabName: {
            type: String,
            required: true
        }
    },
    data() {
        return {
            widgets: [],
            viewHistroyData: [],
            listheaderbuttons: [],            
            LegendArray: [],
            SortBy: '',
            SortExp: '',
            TotalRecord: 0,
            PageNumber: 1,
            PageSize: 10,
            Header: [
                {
                    "COLUMN_NAME": "Audit_Name",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t("AuditorName"),
                    "DISPLAY_ORDER": 1,

                },
                {
                    "COLUMN_NAME": "audit_date",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t("AuditDate"),
                    "DISPLAY_ORDER": 2,
                    settings: {
                        isInSlot: true
                    }

                },
                {
                    "COLUMN_NAME": "comment",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t("Comment"),
                    "DISPLAY_ORDER": 3,

                },
                {
                    "COLUMN_NAME": "StatusId",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Status",
                    "DISPLAY_ORDER": 4,
                    settings: {
                        isInSlot: true
                    }
                },

            ],
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "DateField",
                            label: "",
                            name: "AuditDate",
                            value: '',
                            placeholder: "10/12/2022 12:AM",
                            validationRules: { "required": true },
                            config: {
                                format: "MM/DD/YYYY", maxDate: (new Date())
                            }
                        },
                        {
                            astype: "DateField",
                            label: "",
                            name: "NextAuditDate",
                            value: '',
                            placeholder: "10/12/2022 12:AM",
                            validationRules: { "required": true },
                            config: { format: "MM/DD/YYYY", minDate: (new Date()) }
                        },
                    ],
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "RadioField",
                            label: "Status",
                            name: "Status",
                            value: '1',
                            placeholder: "",
                            validationRules: { "required": false },
                            config: {
                                options: [
                                    { value: "1", name: "Pass" },
                                    { value: "0", name: "Fail" },
                                ],
                                onChange: this.OnChangeStatus
                            }
                        },
                        {
                            astype: "TextAreaField",
                            label: this.$t('Comment'),
                            name: "Comment",
                            value: "",
                            validationRules: { "required": true, max: 1000 },
                            placeholder: ""
                        },
                    ]


                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },

                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.ClosePopup
                }
            ]
        }
    },
    created: function () {

        var vm = this;

        vm.SetLabel();
        vm.FetchData();
        vm.$parent.FetchData();
    },
    methods: {
        OnChangeStatus(event, field) {
            field.value = event.target.getAttribute('data-value');
        },
        SetLabel() {
            this.FormSchema[0].Data[0].label = `${this.TabName} Date (M/D/YYYY)`;
            this.FormSchema[0].Data[1].label = `Next ${this.TabName} Date (M/D/YYYY)`;
        },
        onSubmit(formfields, val) {
            var vm = this;
            let myObj = {
                AuditDate: formfields.AuditDate,
                NextAuditDate: formfields.NextAuditDate,
                Status: formfields.Status,
                Comment: formfields.Comment,
                Historytype: vm.TabName,
                RelatedInfoID: vm.HistoryItems.RelatedInfoId,
            }
            DataService.UpdateAuditHistory(myObj).then(response => {
                if (response.data != null) {
                    if (response.data.length > 0) {
                        vm.FormSchema[0].Data[0].value = '';
                        vm.FormSchema[0].Data[1].value = '';
                        vm.FormSchema[1].Data[1].value = '';
                        vm.$refs.Audithistory.validateForm.reset();
                        vm.ShowAlert(vm.$t(vm.TabName + 'Date successfully added.'), "success", vm.$t('Alert'));

                        vm.FetchData();
                        vm.$parent.FetchData();




                    }

                    else {
                        vm.ShowAlert(vm.$t("Unknown Error Occur"), "failure", true, vm.$t('Alert'));
                        vm.$parent.CloseAuditpopup();
                    }
                }
                setTimeout(function () { vm.CheckBoxBootstrap() }, 100);

            })

        },
        FetchData() {

            var vm = this;
            // var params=`RelatedInfoId=${vm.HistoryItems.RELATED_INFO_ID}&HistoryType=${vm.TabName}`;
            var params = `RelatedInfoId=${vm.HistoryItems.RelatedInfoId}&HistoryType=${vm.TabName}`;

            DataService.GetAuditHistory(params).then(response => {
                if (response.data != null) {
                    if (response.data.data.length > 0) {
                        console.log(response.data.data);
                        vm.viewHistroyData = response.data.data;
                        vm.TotalRecord = response.data.data[0].TOTAL;
                        vm.TotalPage = Math.ceil(vm.TotalRecord / vm.PageSize);
                        vm.CurrentPage = vm.PageNumber;
                        vm.noRecord = false;
                        vm.isLoading = false;
                    }
                    else {
                        vm.noRecord = true;
                        vm.viewHistroyData = [];
                        vm.TotalRecord = 0
                        vm.isLoading = false;
                    }
                }
            });
        },
        ClosePopup() {
            var vm = this;
            vm.TabName = '';
            vm.HistoryItems = [];
            vm.$parent.isHistoryModel = false;

        },


        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNum = value.PageNumber;
            this.FetchData();
        },
        commonHeaderButtonClick: function () { },
        renderActions() { },
        renderRowActionMethod() { },

    }
}
</script>
<style>
.my-popups .modal-content .expand-list {
    z-index: -86 !important;
    }
</style>